.itemInput {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.itemInput label {
  font-weight: bold;
  font-size: 0.9rem;
  margin-right: 0.5rem;
}

.itemInput input {
  width: 2.5rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  padding-left: 0.5rem;
  font: inherit;
}

@media (max-width: 600px) {
  .itemInput {
    display: none;
  }
}
